<template>
  <div>
    <!-- header begin -->
    <!-- <div class="nav-bar">
      <div class="nav-bar-left" @click="goBack()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">发货信息</div>
      <div class="nav-bar-right" @click="showPopup()">
        <i class="iconfont iconsousuo" />
      </div>
    </div> -->
    <!-- header end -->
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <div class="container">
      <common-list
        ref="dida_list"
        class="common-list common-list-spe"
        :post-url="list.postUrl"
        :req-data="list.reqData"
        :data-name="list.dataName"
        :apptype-show="false"
        @returnData="getPrizeLog($event)"
      >
        <div v-for="(item,index) of GysFhxxList" :key="index" class="content">
          <div class="common-main box">
            <div class="common-list common-list-spe">
              <div class="list">
                <h3>{{ item.fhdmc }}</h3>
                <div :class="item.showMore == true ? 'list-flex active':'list-flex'">
                  <div class="info">
                    <span class="label">{{ item.zt }}</span>
                    <span>
                      <i class="iconfont iconbianma" />
                      发货单编号：{{ item.fhdbh }}
                    </span>
                    <span>
                      <i class="iconfont iconwuliuqiache" />
                      物流方式：{{ item.wllx }}
                    </span>
                    <span>
                      <i class="iconfont iconwuliuqiache" />
                      物流公司：{{ item.wlgsmc }}
                    </span>
                    <span>
                      <i class="iconfont icondaibanshixiang" />
                      物流单据编号：{{ item.ydh }}
                    </span>
                    <span>
                      <i class="iconfont iconlianxiren" />
                      送货联系人：{{ item.wllxr }}
                    </span>
                    <span>
                      <i class="iconfont iconshijian" />
                      发货时间：{{ zhTime(item.fhsj) }}
                    </span>
                    <span>
                      <i class="iconfont iconshijian" />
                      签收时间：{{ zhTime(item.dhsj) }}
                    </span>
                  </div>
                  <div :class="item.showMore == true ? 'info-more active':'info-more'" @click="showMoreInfo(index)"><i class="iconfont iconarrow-down" /></div>
                </div>
                <div class="list-btn">
                  <ul>
                    <li v-if="item.zt==='草稿'"><van-button type="default" plain size="small" @click="goShglAdd(item.id)"><i class="iconfont iconxieruqianming" /> 修改</van-button></li>
                    <li v-if="item.zt==='草稿'"><van-button type="default" plain size="small" @click="submit(item.id)"><i class="iconfont iconhegezhihuizonglei" /> 提交</van-button></li>
                    <li v-if="item.zt==='草稿'"><van-button type="default" plain size="small" @click="deletGysFhxx(item.id)"><i class="iconfont iconshanchu" /> 删除</van-button></li>
                    <li v-if="item.zt!=='草稿'"><van-button type="default" plain size="small" @click="goView(item.id)"><i class="iconfont iconxiangqing" /> 查看</van-button></li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </common-list>
    </div>
    <searchMeun @goSearchAction="search($event)" @reSet="goReset">
      <van-field v-model="fhdmc" label="发货单名称" placeholder="请输入发货单名称" input-align="right" />
      <van-field v-model="fhdbh" label="发货单编号" placeholder="请输入发货单编号" input-align="right" />
      <van-cell title="发货时间" is-link :value="fhsj" @click="showFhCalendar()" />
      <van-cell title="状态" is-link :value="ztShow" @click="showZtPopup()" />
      <van-cell title="签收时间" is-link :value="qssj" @click="showDhCalendar()" />
      <van-cell title="是否过磅" is-link :value="sfgbShow" @click="showSfgbPopup()" />
    </searchMeun>
    <!-- 查询条件  begin -->
    <!-- <van-popup v-model="show" position="top" class="search-popup" overlay-class="transparent-overlay">
      <div class="common-main box">
        <div class="spe-input">
          <van-field v-model="fhdmc" label="发货单名称" placeholder="请输入发货单名称" input-align="right" />
          <van-field v-model="fhdbh" label="发货单编号" placeholder="请输入发货单编号" input-align="right" />
          <van-cell title="发货时间" is-link :value="fhsj" @click="showFhCalendar()" />
          <van-cell title="状态" is-link :value="ztShow" @click="showZtPopup()" />
          <van-cell title="签收时间" is-link :value="qssj" @click="showDhCalendar()" />
          <van-cell title="是否过磅" is-link :value="sfgbShow" @click="showSfgbPopup()" />
        </div>
        <div class="search-popup-btn">
          <van-button type="info" round plain block @click="goReset()">重置</van-button>
          <van-button type="info" block round @click="search()">搜索</van-button>
        </div>
      </div>
    </van-popup> -->
    <!-- 查询条件  end -->

    <!-- 状态弹出层 beign -->
    <van-popup v-model="ZtShow" position="bottom" round>
      <van-picker :columns="ZtColumns" show-toolbar @cancel="ZtShow=false" @confirm="onClick" />
    </van-popup>
    <!-- 状态弹出层 end -->

    <!-- 发货日期选择 begin -->
    <van-calendar :key="key1+'fh'" v-model="FhShow" type="range" color="#1890FF" :min-date="mindate" @confirm="onConfirm" />
    <!-- 发货日期选择 begin -->

    <!-- 到货日期选择 begin -->
    <van-calendar :key="key2+'dh'" v-model="DhShow" type="range" color="#1890FF" :min-date="mindate" @confirm="onConfirm1" />
    <!-- 到货日期选择 begin -->

    <!-- 是否过磅弹出层 beign -->
    <van-popup v-model="SfgbShow" position="bottom" round>
      <van-picker :columns="SfgbColumns" show-toolbar @cancel="SfgbShow = false" @confirm="onClick1" />
    </van-popup>
    <!-- 是否过磅弹出层 end -->

    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn fixed">
      <ul>
        <li @click="goShglAdd('')"><van-button type="info" round size="large">新增</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->
  </div>
</template>

<script land="script" scoped>
import commonList from '@/components/list/commonList.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
import headerBar from '@/components/header/headerBar.vue'
export default {
  components: {
    commonList,
    searchMeun,
    headerBar
  },
  data() {
    return {
      headerData: {
        title: '发货信息',
        leftUrl: 'back',
        leftText: ' ',
        showSearch: true
      },
      key1: 1,
      key2: 1,
      mindate: new Date(2010, 0, 1),
      pageno: 1, // 页
      GysFhxxList: [], // 供应商发货信息列表
      date: '2/12 - 2/14',
      value1: '',
      value2: '',
      show: false,
      ZtShow: false,
      FhShow: false,
      DhShow: false,
      SfgbShow: false,
      ZtColumns: ['请选择状态', '草稿', '配送中', '已收货', '已退回'],
      SfgbColumns: ['请选择是否过磅', '是', '否'],
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      fhdmc: '',
      fhdbh: '',
      fhsj: '请选择发货时间',
      qssj: '请选择签收时间',
      fhsjStart: '',
      fhsjEnd: '',
      zt: '',
      dhsjStart: '',
      dhsjEnd: '',
      sfgb: '',
      ztShow: '请选择状态',
      sfgbShow: '请选择是否过磅',
      ztMap: {
        '草稿': 0,
        '配送中': 1,
        '已收货': 2,
        '已退回': -1,
        '请选择状态': ''
      }
    }
  },
  computed: {

  },
  created() {
    this.init()
  },
  methods: {
    goReset() {
      this.key1++
      this.key2++
      this.fhdmc = ''
      this.fhdbh = ''
      this.fhsj = '请选择发货时间'
      this.ztShow = '请选择状态'
      this.qssj = '请选择签收时间'
      this.sfgbShow = '请选择是否过磅'
      this.fhsjStart = ''
      this.fhsjEnd = ''
      this.zt = ''
      this.dhsjStart = ''
      this.dhsjEnd = ''
      this.sfgb = ''
    },
    onClick1(value) {
      this.sfgbShow = value
      if (value === '是') {
        this.sfgb = 1
      } else if (value === '否') {
        this.sfgb = 0
      } else {
        this.sfgb = ''
      }
      this.SfgbShow = false
    },
    onClick(value) {
      this.ztShow = value
      this.zt = this.ztMap[value]
      this.ZtShow = false
    },
    search() {
      this.$bus.$emit('showMeun', false)
      const msg = {
        fhdmc: this.fhdmc,
        fhdbh: this.fhdbh,
        fhsjStart: this.fhsjStart,
        fhsjEnd: this.fhsjEnd,
        zt: this.zt,
        dhsjStart: this.dhsjStart,
        dhsjEnd: this.dhsjEnd,
        sfgb: this.sfgb
      }
      this.$refs.dida_list.onRefresh(msg)
      this.show = false
    },
    init() {
      this.list.postUrl = 'fhgl/getXm_fhdhListAction'
      this.list.reqData = {
        ryid: this.$store.state.mh.mhUserInfo.id,
        pageno: this.pageno
      }
      this.list.dataName = 'list'
    },
    getPrizeLog(data) {
      this.GysFhxxList = data
    },
    goView(fhbh) {
      this.$store.commit('fhgl/SET_FHBH', fhbh)
      this.$router.push({
        name: 'ShglView'
      })
    },
    goBack() {
      this.$router.back()
    },
    submit(fhbh) {
      this.$dialog.confirm({
        title: '提示',
        message: '确认提交当前信息么？'
      }).then(() => {
        this.$store.dispatch('fhgl/submitXm_fhdhAction', {
          fhbh: fhbh,
          ryid: this.$store.state.mh.mhUserInfo.id
        }).then((res) => {
          this.$toast('提交成功！')
          this.$refs.dida_list.onRefresh()
        }).catch((res) => {
          this.$toast(res)
        })
      }).catch(() => {
      })
    },
    deletGysFhxx(fhbh) {
      this.$dialog.confirm({
        title: '提示',
        message: '确认删除当前信息么？'
      }).then(() => {
        this.$store.dispatch('fhgl/deleteXm_fhdhAction', {
          fhbh: fhbh
        }).then((res) => {
          this.$toast('删除成功！')
          this.$refs.dida_list.onRefresh()
        })
      }).catch(() => {
      })
    },
    zhTime(time) {
      if (time) {
        time = time + ''
        return time.slice(0, 4) + '-' + time.slice(4, 6) + '-' + time.slice(6, 8)
      }
    },
    showPopup() {
      this.show = true
    },
    showZtPopup() {
      this.ZtShow = true
    },
    showFhCalendar() {
      this.FhShow = true
    },
    showDhCalendar() {
      this.DhShow = true
    },
    showSfgbPopup() {
      this.SfgbShow = true
    },
    formatDate(date) {
      const year = date.getFullYear()
      const month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : date.getMonth()
      const day = date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()
      return year + '-' + month + '-' + day
    },
    formatDate1(date) {
      const year = date.getFullYear()
      const month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : date.getMonth()
      const day = date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()
      return year + '' + month + '' + day
    },
    onConfirm(date) {
      const [start, end] = date
      this.FhShow = false
      this.fhsj = `${this.formatDate(start)} - ${this.formatDate(end)}`
      this.fhsjStart = `${this.formatDate1(start)}`
      this.fhsjEnd = `${this.formatDate1(end)}`
    },
    onConfirm1(date) {
      const [start, end] = date
      this.DhShow = false
      this.qssj = `${this.formatDate(start)} - ${this.formatDate(end)}`
      this.dhsjStart = `${this.formatDate1(start)}`
      this.dhsjEnd = `${this.formatDate1(end)}`
    },
    showMoreInfo(index) {
      this.GysFhxxList[index].showMore = !this.GysFhxxList[index].showMore
      this.GysFhxxList.splice(index, 1, this.GysFhxxList[index])
    },
    goShglAdd(fhbh) {
      this.$store.commit('fhgl/SET_FHBH', fhbh)
      this.$router.push({
        name: 'ShglAdd'
      })
    }
  }

}
</script>

<style lang="css" scoped>

</style>
